<template>
  <nav>
    <div class="nav-wrapper blue-grey darken-4">
        <div class="logo" v-on:click="reloadRoute">
          <router-link to="" class="brand-logo"><img src="@/assets/geopogo.png" alt=""></router-link>
        </div>
        <ul class="right hide-on-med-and-down">
          <li v-if="isLoggedIn" class="email">{{currentUser}}</li>
          <li v-on:click="reloadRoute"><router-link to="/downloadLink">Demo</router-link></li>
          <li><router-link to="/pricing">Buy Now</router-link></li>
          <li><a href="collapsible.html">About Us</a></li>
          <li v-if="!isLoggedIn"><router-link to="/LoginSign">Sign Up/Login</router-link></li>
          <li v-if="isLoggedIn"><button v-on:click="logout" class="btn black">Logout</button></li>
        </ul>
      </div>
  </nav>
</template>

<script>
import AOS from "aos";
import "aos/dist/aos.css"
import firebase from 'firebase';
export default {
  name: 'Header',
  data () {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentUser = firebase.auth().currentUser.email;
    }
  },
  methods:{
    logout: function(){
      firebase.auth().signOut().then(() => {
        this.$router.go({ path: this.$router.path });
      });
    },
    reloadRoute: function(){
      AOS.refreshHard();
      this.$router.replace('/')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  img{
    position: relative;
    height: 43%;
    width: 42%;
    margin-left: 5vw;
    margin-top: .2%;
  }

  a{
    z-index: 50;
  }

  ul{
    z-index: 500;

    top: 50%;
  }

  nav{
    position: fixed;
    z-index:10;

  }

  .email{
    padding-right: 5vh;
  }

  ul{
    padding-right: 10vh;
    z-index: 50;
  }

  .logo{
    z-index: -1;
  }

  .brand-logo{
    z-index: 0;
  }

  li{
    z-index: 50;
    top: 50%;
    bottom: 0;
  }

  #nav-mobile{
    z-index: 50;
    margin-top: -1.5%;
    margin: auto;
    /*position: relative;
    bottom: 50%;*/
  }


</style>



